import { Component, OnInit, ViewChild } from '@angular/core';
import { PurchaseUser } from '../../_models/PurchaseUser';
import { LoadspecsService } from '../../_services/loadspecs.service';
import { AlertifyService } from '../../_services/alertify.service';
import { ModalDirective } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-purchase-users',
  templateUrl: './purchase-users.component.html',
  styleUrls: ['./purchase-users.component.css']
})
export class PurchaseUsersComponent implements OnInit {
  model: any = {};
  mymodel: any = {};
  users: PurchaseUser[];
  roleid: any =  Number(localStorage.getItem('RoleID'));
  @ViewChild('addModal') public addModal: ModalDirective;
  @ViewChild('EditModal') public EditModal: ModalDirective;

  constructor(private loadspecsService: LoadspecsService, private alertify: AlertifyService) { }

  ngOnInit(): void {
    this.loadPurchaseUsers();
  }
  loadPurchaseUsers() {
    return this.loadspecsService.getPurchaseUsers().subscribe((users: PurchaseUser[]) => {
      this.users = users;
    }, error => {
      this.alertify.error(error);
    });
  }
  AddPurchaseUsers() {
    if (!this.model.p_name) {
      this.alertify.error('Write Name');
      return;
    }
    if (!this.model.email) {
      this.alertify.error('Write Email');
      return;
    }
    if (!/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(this.model.email)) {
      this.alertify.error('Enter valid email');
      return;
    }
    if (!this.model.type) {
      this.alertify.error('Select Type');
      return;
    }
    this.loadspecsService.AddPurchaseUsers(this.model).subscribe((users: PurchaseUser[]) => {
      this.users = users;
      this.alertify.success('Purchase User Added Successful');
      this.addModal.hide();
      this.model.p_name = '';
      this.model.email = '';
    }, error => {
      this.alertify.error(error);
    });
  }
  EditPurchaseUsers(id: number) {
    this.EditModal.show();
    this.mymodel = this.users.filter(proj => (proj.id === id))[0];
  }
  EditPurchaseUserPost(id: number) {
    if (!this.mymodel.p_name) {
      this.alertify.error('Write Name');
      return;
    }
    if (!this.mymodel.email) {
      this.alertify.error('Write Email');
      return;
    }
    if (!/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(this.mymodel.email)) {
      this.alertify.error('Enter valid email');
      return;
    }
    if (!this.mymodel.type) {
      this.alertify.error('Select Type');
      return;
    }
    this.loadspecsService.EditPurchaseUserPost(this.mymodel).subscribe((users: PurchaseUser[]) => {
      this.users = users;
      this.alertify.success('User Edited Successfuly');
      this.EditModal.hide();
    }, error => {
      this.alertify.error(error);
    });
  }
  DeletePurchaseUsers (id: number) {
    this.mymodel.id = id;
    this.loadspecsService.DeletePurchaseUsers(this.mymodel).subscribe((users: PurchaseUser[]) => {
      this.users = users;
      this.alertify.success('User Deleted Successfuly');
      this.EditModal.hide();
    }, error => {
      this.alertify.error(error);    });

  }

}
